import React from 'react';


  const ContactDetails = ({ contacts, deleteContact }) => {
    return (
      <ul>
      {contacts.map(person => 
        <li key={person.name}>
         {person.name}, {person.number}
         <button onClick={() => deleteContact(person)}>delete</button>
        </li>
      )}
   </ul>
    );
  };

  export default ContactDetails;