import React, { useState, useEffect } from "react";
import request from "./request";
import ContactDetails from './ContactDetails'

const PersonInfo = ({ input, handler }) => {
  return (
    <div>
      <input value={input} onChange={handler} />
    </div>
  );
};



const App = (props) => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState("");
  const [number, setNumber] = useState("");
  const [success,setSuccessMsg]=useState("")

  useEffect(() => {
    request.getAllNumbers().then((persons) => {
      setPersons(persons);
    });
  }, []);


  const checkReplica = (text) => {
    return persons.find((item) => item.name === text);
  };

  const deleteContact = (person ) => {
      if (window.confirm(`Delete ${person.name} ?`)) {
        request.deleteContact(person.id)
        setPersons(persons.filter(contact=>contact.id !==person.id))
      }
    }
  

  const submitInput = (event) => {
    event.preventDefault();
    const newNumber = {
      name: newName,
      number: number,
    };
    checkReplica(newName)
      ? alert(`${newName} already exists`)
      : ( request.createNumber(newNumber)
      .then(res => {
        setPersons(persons.concat(res));  
        setSuccessMsg(`Successfully added ${newName} `)
        setTimeout(()=>setSuccessMsg(""),2000)
        setNewName('');
        setNumber('');
      }))
  };

  const onTextChange = (event) => {
    setNewName(event.target.value);
  };
  const onChangeNumber = (event) => {
    setNumber(event.target.value);
  };

  const SuccessNotify = () => {
    
    return(
      <div style={styles.success}>
          {success}
      </div>)
  }

  return (
    <div>
    {success &&  
    <SuccessNotify/>  
    }
      <h2>Phonebook</h2>
      <form>
        name:<PersonInfo input={newName} handler={onTextChange} /> 
        <br />
        number:<PersonInfo input={number} handler={onChangeNumber} />
        <div>
          <button type="submit" onClick={submitInput}>
            Add
          </button>
        </div>
      </form>
      <h2 style={styles.header}>Numbers</h2>
          <ContactDetails
          contacts={persons}
          deleteContact={deleteContact}        
          />
    </div>
  );
};


const styles = {
  success :{
  color: 'green',
  fontSize: 16 ,
  borderWidth : 1,
  innerWidth:200,
  borderColor:'red',
  padding:20,
  borderRadius:5,
  borderStyle:'solid'
},
failure :{
  color:'red',
  fontSize:30
}

}

export default App;
